.Navigation {
  color: #d3d3d3;
  background-color: #000000;
  width: 100%;
  height: 16px;
  padding: 5px;
  box-align: middle;
  position: relative;
  top: 0;
  z-index: 999;
}

ul.NavigationMenus {
  display: flex;
  align-items: center;
  padding: 0;
}

ul.NavigationMenus li {
  list-style: none;
  margin-right: 30px;

}

ul.NavigationMenus li a {
  text-decoration: none;
  font-size: 14px;
  color: #fff;
  position: relative;
  padding-bottom: 5px;
}

ul.NavigationMenus li a:after {
  content: "";
  display: block;
  position: absolute;
  background: #fff;
  height: 1px;
  width: 0%;
  bottom: 0;
  transition: 0.3s;
  margin: 0px auto;
  left: 0;
  right: 0;
}

ul.NavigationMenus li a:hover:after {
  width: 100%;
  transition: 0.3s;
}

.activeNavItem a:after {
  content: "";
  display: block;
  position: absolute;
  background: #fff;
  height: 1px;
  width: 100% !important;
  bottom: 0;
  transition: 0.3s;
  margin: 0px auto;
  left: 0;
  right: 0;
  color: hotpink !important;
}


li.activeNavItem a:after {
  background: #f2f2f270 !important;
}



