
.container {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 15px;
  float:left;
  border-collapse: separate;
  border-spacing: 0 15px

}

    
    fragment.blur {
      filter: blur(5px);
    }
    .fragment.blur.visible {
      filter: none;
      
    }
    
   
.grow {
  border-radius: 15px;
  height: 49px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  transition: height 0.5s;
  -webkit-transition: height 0.5s;
  text-align: center;
  overflow: hidden;
}
.grow:hover {
  height: 145px;
}